@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

:root {
  --primary-o: #f0ae8e;
  --primary-g: #c7d9a7;
  --accent-b: #d0efe9;
  --accent-y: #fff4e3;
  --accent-p: #facaca;
  --text: #083132;
  --background-w: #ffffff;
  --background-b: #f9f6e3;
}

.main {
  margin: 0px;
  overflow: hidden;
}
ul.nav {
  display: flex;
  margin: 0;
  padding: 0;

  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.circle {
  height: 48px;
  width: 48px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("./assets/circle.svg");
}
.highlight {
  background-image: url("./assets/wave.svg");
  padding-left: 5px;
  background-position: 0 20px;
  background-repeat: repeat-x;
}

img.hand {
  position: relative;
  top: 50%;
  left: 49%;
  margin-bottom: 81px;
  animation: waving infinite 2s linear 5s;
}

@keyframes waving {
  0% {
    transform: rotate(15deg);
  }
  50% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(15deg);
  }
}

/* Navigation Bar */

div.top {
  margin-top: 10vh;
}

div.greeting {
  width: 100%;
}

#itiswell {
  height: 600px;
  background-color: var(--accent-y);
}

div.column {
  float: left;
  width: 50%;
  height: 600px;
}
div.row:after {
  display: table;
  clear: both;
}
#bbla {
  background-color: var(--primary-g);
}

#kiiboo {
  background-color: var(--primary-o);
}

@media (max-width: 800px) {
  ul.nav {
    flex-direction: column;
  }
  ul.nav button {
    margin-bottom: 1em;
  }
  div.column {
    float: left;
    width: 100%;
    height: 600px;
  }
}
